import React, { Component } from "react";

import canaan1 from "../../asset/canaan/canaan_homepage.png";
import canaan2 from "../../asset/canaan/post_a_job.png";
import canaan3 from "../../asset/canaan/sign_up_function.png";

import contactManager1 from "../../asset/contactManager/contactManager1.png";
import contactManager2 from "../../asset/contactManager/contactManager2.png";
import contactManager3 from "../../asset/contactManager/contactManager3.png";

import clientPanel1 from "../../asset/contactManager/contactManager1.png";
import clientPanel2 from "../../asset/contactManager/contactManager2.png";
import clientPanel3 from "../../asset/contactManager/contactManager3.png";

export default class ProjectPanel extends Component {
  state = {
    project_list: [
      {
        "project-name": "CanaanJobs",
        "project-img": [canaan1, canaan2, canaan3],
        "project-link": "",
        "project-github": "https://github.com/khsyo/connect-the-x",
        "project-caption": ["caption1", "caption2", "caption3"]
      },
      {
        "project-name": "Contact Manager",
        "project-img": [contactManager1, contactManager2, contactManager3],
        "project-link": "https://khsyo.github.io/contactmanager/#/",
        "project-github": "https://github.com/khsyo/contactmanager",
        "project-caption": ["caption1", "caption2", "caption3"]
      },
      {
        "project-name": "Client Panel",
        "project-img": [clientPanel1, clientPanel2, clientPanel3],
        "project-link": "https://khsyo.github.io/reactclientpanel/#/",
        "project-github": "https://github.com/khsyo/contactmanager",
        "project-caption": ["caption1", "caption2", "caption3"]
      }
    ]
  };
  render() {
    const project_list = this.state.project_list;
    const project_panel = project_list
      ? project_list.map(p => (
          <div className="project-panel--item">
            <h4>{p["project-name"]}</h4>
            <div className="project-panel--img-div">
              <img
                className="project-panel--img"
                src={p["project-img"][0]}
                alt="project image"
              />
            </div>

            <h6>
              Hosted on: <a href={p["project-link"]}>{p["project-link"]}</a>
            </h6>
            <h6>
              Github: <a href={p["project-github"]}>{p["project-github"]}</a>
            </h6>
          </div>
        ))
      : null;
    return <div className="project-panel">{project_panel}</div>;
  }
}
